.coaches-slider__content {
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-top: 40px;
    padding-bottom: 120px;

    @include tablet {
      padding-top: 38px;
      padding-bottom: 100px;
    }

    @include mobile {
      padding-top: 26px;
      padding-bottom: 70px;
    }

    h2 {
      @include h2;
      margin-right: auto;
      margin-bottom: 50px;      

      @include tablet {
        margin-right: 0;
        margin-bottom: 40px;
      }

      @include mobile {
        margin-bottom: 42px;
      }
    }
}

.coaches-slider__container {
    position: relative;
}

.coaches-slider__button {
    position: absolute;
    bottom: 130px;
    padding-left: 0;
    padding-right: 0;
    width: 18px;
    height: 32px;
    border-radius: 3px;
    background: url("../img/icons/arrow.svg");
    cursor: pointer;
    outline: none;
    
    &:hover,
    &focus {
      position: absolute;
      bottom: 130px;
      padding-left: 0;
      padding-right: 0;
      width: 18px;
      height: 32px;
      background: url("../img/icons/arrow-blue.svg");

      @include mobile {
        width: 14px;
        height: 22px;
        bottom: 126px;
      }
    }

    @include mobile {
      width: 14px;
      height: 22px;
      bottom: 126px;
    } 

    &:first-of-type {
      left: -55px;
      transform: rotate(180deg);

      @include tablet {
       left: -60px;
      }

      @include mobile {
        left: -19px;
      }
    }

    &:last-of-type {
      right: -55px;

      @include tablet {
        right: -60px;
      }

      @include mobile {
        right: -19px;
      }
    }
}

.coaches-slider__list-wrapper {
    width: 1160px;
    overflow: hidden;

    @media screen and (max-width: 1364px) {
      width: 1100px;
    }

    @media screen and (max-width: 1300px) {
      width: 1020px;
    }

    @include tablet {
      width: 856px;
    }

    @media (max-width: 1023px) {
      width: 559px;
    }

    @include mobile {
      width: 256px;
    }
}

.coaches-slider__list {
    display: flex;
    transition-duration: 0.4s;

    > li {
      display: inline-block;
      flex-shrink: 0;
      margin-right: 40px;

      @media screen and (max-width: 1365px) {
        margin-right: 20px;
        height: 294px;
      }

      @include tablet {
        margin-right: 30px;
      }

      @include mobile {
        height: 274px;
        width: 256px;        
        margin: 0;
      }
    }
}